/**
 * BlogContainer.js
 * Get blog data here and render in blog view
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import Blog from '../components/Blog/Blog';

import { fetchBlogFeed } from '../actions/blogActions';

class BlogContainer extends Component {
  componentDidMount() {
    const { getBlogData } = this.props;
    getBlogData();
  }

  render() {
    const { blogData } = this.props;
    // console.log('blog', blogData);
    return <Blog blogData={blogData} num={5} />;
  }
}

const mapStateToProps = state => ({
  blogData: state.blog.blogData,
});

const mapDispatchToProps = dispatch => ({
  dispatch,
  getBlogData: () => {
    dispatch(fetchBlogFeed());
  },
});

BlogContainer.defaultProps = {
  blogData: null,
};

BlogContainer.propTypes = {
  getBlogData: PropTypes.func.isRequired,
  blogData: PropTypes.object,
};

export default connect(mapStateToProps, mapDispatchToProps)(BlogContainer);
