import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import PropTypes from 'prop-types';

import { Col } from 'reactstrap';

// import LandingBlogFront from 'assets/images/Landing/blog-bg-image.svg';
import Aeroplane from 'assets/images/Landing/aeroplane.svg';
import 'assets/styles/generic.scss';

import './Blog.scss';


class Blog extends Component {
  renderMain(mainData) {
    return (
      <div className="blog-main">
        <Col xs={12} md="7">
          <a className="blog-img-main" href={mainData.link} target="_blank">
            <LazyLoadImage
              className="blog-img"
              alt="blog-main-img"
              // height={image.height}
              src={mainData.img}
              // width={image.width}
            />
          </a>
        </Col>
        <Col md="5" className="flex-justify">
          <p>
            <a
              href={mainData.link}
              target="_blank"
              className="blog-title blog-main-title"
              dangerouslySetInnerHTML={{ __html: mainData.title || '&nbsp;'}}
            />
          </p>
          {mainData.date ? (
            <div>
              <p className="blog-date">{mainData.date}</p>
            </div>
          ) : null}
          {mainData.excerpt ? (
            <p
              className="blog-text"
              dangerouslySetInnerHTML={{__html: `
                ${mainData.excerpt}
                <a
                  className="blog-text-link"
                  href="${mainData.link}"
                  target="_blank"
                >
                  &nbsp;Read More
                </a></Col>
              `}}
            />
          ) : null}
        </Col>
      </div>
    );
  }

  renderSub(subData) {
    return _.map(subData, ({
      date, link, img, title,
    }) => (
      <Col md="4" className="blog-sub-container" key={title}>
        <p>
          <a href={link} target="_blank">
            <img className="blog-img blog-img-sub" src={img} alt={title} />
          </a>
        </p>

        <p>
          <a
            href={link}
            target="_blank"
            className="blog-title blog-main-sub"
            dangerouslySetInnerHTML={{ __html: title || '&nbsp;'}}
          />
        </p>

        {date ? <p className="blog-date">{date}</p> : null}
      </Col>
    ));
  }
  render() {
    const { blogData } = this.props;

    if (blogData && blogData.blogContentMain && blogData.blogContentSub) {
      return (
        <Fragment>
          <div className="blog-container">
            <img src={Aeroplane} className="blog-aeroplane" alt="aeroplane" />
            {this.renderMain(blogData.blogContentMain)}
            {this.renderSub(blogData.blogContentSub)}
          </div>
          {/* <img src={LandingBlogFront} className="blog-front-image" alt="landing-front.png" /> */}
        </Fragment>
      );
    }
    return null;
  }
}

Blog.defaultProps = {
  blogData: null,
};

Blog.propTypes = {
  blogData: PropTypes.object,
};

export default Blog;
